import React from "react";
import logo from "./logo.svg";
import "../node_modules/bootstrap/dist/css/bootstrap.min.css";
import "./App.css";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import Login from "./pages/Login";
import SignUpOtp from "./pages/SignUpOtp";
import LoginOtp from "./pages/Login-otp";
import SignUp from "./pages/Signup";
import MainPage from "./pages/MainPage";
import ExtendedHome from "./pages/extendedHome";
import ExpertList from "./pages/ExpertList";
import ExpertProfile from "./pages/ExpertProfile";
import ContactUs from "./pages/ContactUs";
import Privacy from "./pages/Privacy";
import ExpertRegistration from "./pages/ExpertRegistration";
function App() {
  return (
    <Router basename={process.env.PUBLIC_URL}>
      <Switch>
        <Route path="/login">
          <Login />
        </Route>
        <Route exact path="/sign-up">
          <SignUp />
        </Route>

        <Route exact path="/login-otp">
          <LoginOtp />
        </Route>
        <Route exact path="/signup-otp">
          <SignUpOtp />
        </Route>
        <Route exact path="/list">
          <ExpertList />
        </Route>
        <Route exact path="/expert-profile">
          <ExpertProfile />
        </Route>
        <Route exact path="/contact-us">
          <ContactUs />
        </Route>
        <Route exact path="/expert-registration">
          <ExpertRegistration />
        </Route>
        <Route exact path="/extend">
          <ExtendedHome />
        </Route>
        <Route exact path="/privacy">
          <Privacy />
        </Route>
        <Route exact path="/">
          <MainPage />
        </Route>
      </Switch>
    </Router>
  );
}

export default App;
