    e.preventDefault();
    let otpInput = "123456";
    let optConfirm = JSON.parse(sessionStorage.getItem("confirmation"));
    console.log(optConfirm);
    optConfirm
      .confirm(otpInput)
      .then(function (result) {
        // User signed in successfully.
        // console.log("Result" + result.verificationID);
        let user = result.user;
      })
      .catch(function (error) {
        console.log(error);
        alert("Incorrect OTP");
      });
