import React, { useState } from "react";
import styled, { keyframes } from "styled-components";
import Facebook from "../img/Facebook login.png";
import Google from "../img/Google login.png";
import photo from "../img/image 2.png";
import { Link, useHistory } from "react-router-dom";
import { bounce, fadeIn } from "react-animations";
import NavBar from "../components/NavbarBasic";
import photo1 from "../img/otp.png";

import OtpInput from "react-otp-input";
import { Spinner } from "react-bootstrap";
import { firebaseApp, fireDb } from "../firebase";
import firebase from "firebase";
const Home = () => {
  const [mobileNo, setMobileNo] = useState("");
  const [isSubmit, setIsSubmit] = useState(false);
  const [otp, setOtp] = useState("");
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(false);
  const [userList, setUserList] = useState([]);
  const [userValid, setUserValid] = useState(false);
  const Bounce = styled.div`
    animation: 2s ${keyframes`${fadeIn}`} 1;
  `;
  let history = useHistory();
  const googlefun = () => {
    let flag = 0;
    const provider = new firebase.auth.GoogleAuthProvider();
    firebase
      .auth()
      .signInWithPopup(provider)
      .then((data) => {
        console.log(data);
        firebaseApp.auth().onAuthStateChanged((user) => {
          sessionStorage.setItem("email", user.email);
          sessionStorage.setItem("photo", user.photoURL);
          sessionStorage.setItem("phno", user.phoneNumber);
          fireDb
            .collection("EXPERT_INVITES")
            .get()
            .then((snapshot) => {
              let arr = [];
              snapshot.docs.forEach((doc) => {
                console.log(doc.data());
                arr.push(doc.data());
              });
              console.log(setUserList(arr));
              console.log(userList);
            })
            .catch((err) => {
              console.log(err);
            });
          console.log(user);
          history.push("/");
        });
      })
      .catch((err) => {
        setError(true);
        console.log(err);
      });
  };
  const facebookfun = () => {
    const provider = new firebase.auth.FacebookAuthProvider();
    firebaseApp
      .auth()
      .signInWithPopup(provider)
      .then((data) => {
        console.log(data);
        firebaseApp.auth().onAuthStateChanged((user) => {
          console.log(user);
        });
        return data;
      })
      .catch((err) => {
        setError(true);
        console.log(err);
        return err;
      });
  };

  const setUpRecaptcha = () => {
    window.recaptchaVerifier = new firebase.auth.RecaptchaVerifier(
      "recaptcha-container",
      {
        size: "invisible",
        callback: function (response) {
          console.log("Captcha Resolved");
          this.onSignInSubmit();
        },
        defaultCountry: "IN",
      }
    );
  };
  const onSignInSubmit = (e) => {
    e.preventDefault();
    setUpRecaptcha();
    let phoneNumber = "+91" + mobileNo;
    {
      /*+91" + mobileNo*/
    }
    console.log(phoneNumber);
    setLoading(true);
    let appVerifier = window.recaptchaVerifier;
    firebase
      .auth()
      .signInWithPhoneNumber(phoneNumber, appVerifier)
      .then(function (confirmationResult) {
        // SMS sent. Prompt user to type the code from the message, then sign the
        // user in with confirmationResult.confirm(code).
        window.confirmationResult = confirmationResult;
        console.log(window.confirmationResult);
        // console.log(confirmationResult);
        console.log("OTP is sent");
        setLoading(false);

        console.log(window.confirmationResult);
        setIsSubmit(true);
      })
      .catch(function (error) {
        setError(true);
        console.log(error);
        setLoading(false);
      });
  };
  const onSubmitOtp = (e) => {
    e.preventDefault();
    let otpInput = otp;
    let optConfirm = window.confirmationResult;
    console.log(optConfirm);
    optConfirm
      .confirm(otpInput)
      .then(function (result) {
        // User signed in successfully.
        // console.log("Result" + result.verificationID);
        let user = result.user;
        sessionStorage.setItem("phno", user.phoneNumber);
        history.push("/");
        console.log(user);
      })
      .catch(function (error) {
        console.log(error);
        alert("Incorrect OTP");
      });
  };
  let show;
  let btnText;
  let errmsg = null;
  if (loading) {
    btnText = <Spinner animation="border" size="sm" />;
  } else {
    btnText = "Submit";
  }
  if (error) {
    errmsg = <p style={{ color: "red" }}>something went wrong !!!</p>;
  }
  if (isSubmit === false) {
    show = (
      <div>
        <NavBar />
        <Wrapper>
          <div className="img-wrapper mb-5">
            <img src={photo} />;
          </div>
          <div className="box-wrapper mb-5">
            <div className="expert-register">
              <p>
                <Link to="expert-registration">
                  Register to become an expert?
                </Link>
              </p>
            </div>
            <div className="box">
              <div className="heading mb-5">
                <h1>Login</h1>
                <p>
                  Don't have account yet ?{" "}
                  <Link to="login-otp">
                    <span>sign-up</span>
                  </Link>
                </p>
              </div>
              <form className="form">
                <label className="mb-1">Mobile Number</label>
                <br />
                <input
                  type="text"
                  className="mob-no"
                  onChange={(e) => setMobileNo(e.target.value)}
                  style={{ color: "white" }}
                ></input>
                <br />
                <button
                  className="sign-up mb-2"
                  onClick={(e) => onSignInSubmit(e)}
                  disabled={loading}
                >
                  {btnText}
                </button>
                {errmsg}

                <div id="recaptcha-container"></div>
              </form>
              <div className="separator mb-4">
                <h2>
                  <span>or Sign-up with</span>
                </h2>
              </div>
              <div className="g-btn-wrapper mb-4">
                <button>
                  <img src={Facebook} onClick={facebookfun} />
                </button>
                <button>
                  <img src={Google} onClick={googlefun} />
                </button>
              </div>
            </div>
          </div>
        </Wrapper>
      </div>
    );
  } else {
    show = (
      <div>
        <NavBar />
        <Wrapper1>
          <div className="img-wrapper mb-5">
            <img src={photo1} />;
          </div>
          <div className="box-wrapper mb-5">
            <div className="box">
              <div className="heading mb-3">
                <h1>Login Otp</h1>
              </div>
              <form className="form">
                <label className="mb-3">Please enter otp</label>
                <br></br>
                <br />
                <div>
                  <OtpInput
                    value={otp}
                    onChange={(otp) => {
                      console.info(otp);
                      setOtp(otp);
                    }}
                    numInputs={6}
                    inputStyle={{
                      color: "white",
                      fontSize: "24px",
                      width: "36px",
                      height: "36px",
                      margin: "4px",
                      borderTop: "0px",
                      borderLeft: "0px",
                      borderRight: "0px",
                      outline: "none",
                      borderColor: "white",
                      backgroundColor: "#3282b7",
                    }}
                    containerStyle={{
                      margin: "20px auto",
                      padding: "10px",
                    }}
                    isInputNum
                  />
                </div>
                <br />
              </form>
              <div className="g-btn-wrapper mb-4">
                <button
                  style={{ backgroundColor: "#0e4c75" }}
                  onClick={(e) => onSubmitOtp(e)}
                >
                  <span>Verify</span>
                </button>
                <button>
                  <span>Resent OTP</span>
                </button>
              </div>
            </div>
          </div>
        </Wrapper1>
      </div>
    );
  }
  return <div>{show}</div>;
};

