import React, { useState } from "react";
import styled from "styled-components";
import NavBar from "../components/NavbarBasic";
import Footer from "./footer";
import { fireDb } from "../firebase";
import { Spinner, Alert } from "react-bootstrap";

const ExpertRegistration = () => {
  const [email, setEmail] = useState("");
  const [name, setName] = useState("");
  const [phNo, setPhNo] = useState("");
  const [loading, setLoading] = useState(false);
  const [success, setSuccess] = useState(false);
  const handleClick = () => {
    setLoading(true);
    fireDb
      .collection("EXPERT_INVITES")
      .add({
        name: name,
        contact: phNo,
        email: email,
      })
      .then((data) => {
        setLoading(false);
        setSuccess(true);
        setTimeout(() => {
          setSuccess(false);
        }, 3000);
        console.log(data);
      })
      .catch((err) => {
        console.log(err);
        setLoading(false);
      });
  };
  let btnText, alert;
  if (loading) {
    btnText = <Spinner animation="border" size="sm" />;
  } else {
    btnText = "Send";
  }
  if (success) {
    alert = (
      <Alert variant="success">
        <p>Send successfully!! </p>
      </Alert>
    );
  } else {
    alert = "";
  }
  return (
    <Wrapper>
      <NavBar />
      <div className="header-section pt-4 text-center mb-5 pb-3">
        <h1 className="mb-4">Become An Expert</h1>
        <h5>Register Register to become a Fexpert expert.</h5>
        <h5>Coming soon</h5>
      </div>
      <div className="container">
        <div className="row mb-4">
          <div className="col-md-6 col-sm-12 mb-4">
            <h3>Send expert invite</h3>
            <p>Send your email to apply to become an expert</p>
            <div className="mb-3">
              <input
                type="text"
                placeholder="email address.."
                className="cust-inp"
                onChange={(e) => setEmail(e.target.value)}
              />
            </div>
            <div className="mb-3">
              <input
                type="text"
                placeholder="Name.."
                className="cust-inp"
                onChange={(e) => setName(e.target.value)}
              />
            </div>
            <div className="mb-3">
              <input
                type="text"
                placeholder="phone no.."
                className="cust-inp"
                onChange={(e) => setPhNo(e.target.value)}
              />
            </div>
            <div className="btn-div mb-2">
              <button className="send-btn" onClick={handleClick}>
                {btnText}
              </button>
            </div>

            {alert}
          </div>

          <div className="col-md-6 col-sm-12">
            <div className="right-section">
              <h3 className="text-center mb-4">What we bring to you</h3>
              <ul>
                <li>
                  Esse irure ea id exercitation mollit nostrud commodo dolor
                  exercitation sint tempor minim ipsum reprehenderit. Esse irure
                  pariatur ullamco proident do proident proident anim
                  adipisicing ex. Duis laboris irure sunt do ullamco qui
                  incididunt pariatur culpa aliqua eiusmod eu. Aute pariatur
                  fugiat qui in Lorem incididunt velit aliquip irure. Nostrud
                  magna labore et eiusmod deserunt cupidatat enim nulla
                  voluptate dolor nulla eu. Excepteur duis excepteur in
                  reprehenderit proident cillum fugiat veniam amet Lorem fugiat
                  officia officia. Dolor nisi duis duis irure pariatur eu
                  incididunt mollit dolor amet eiusmod et amet consequat.
                </li>
                <li>
                  Eu dolor ex excepteur occaecat consectetur velit voluptate
                  dolore id consequat. Qui dolore aliquip tempor veniam est ad
                  eiusmod. Magna eu pariatur enim fugiat.
                </li>
                <li>
                  Sint nostrud nostrud aliquip qui ex qui fugiat do excepteur
                  pariatur officia dolor nostrud. Dolor minim id sunt nulla amet
                  labore. Nulla dolor et duis excepteur anim nisi non et labore
                  proident labore laboris est cupidatat.
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
      <Footer />
    </Wrapper>
  );
};
const Wrapper = styled.div`
  .header-section {
    background-color: #1b262c;
    color: white;
  }
  .btn-div {
    width: 100%;
    display: flex;
  }
  .send-btn {
    border: none;
    background-color: #0e4c75;
    color: white;

    padding: 0.2rem 1rem;
    :hover {
      background-color: white;
      color: #0e4c75;
    }
  }
  .email-wrapper {
    display: flex;
    button {
      padding: 10px;
      border: none;
      background-color: #14bef0;
      color: white;
      &:hover {
        background-color: white;
        color: #14bef0;
      }
    }
  }
  .cust-inp {
    border: none;
    width: 90%;
    background-color: #e4e1e1;
    padding: 0.5rem;
  }
  .right-section {
    background-color: #dcd8d8;
    padding: 1.5rem;
    color: black;

    ul {
      list-style: none;
      padding: 0;
    }
    li {
      margin-bottom: 1.5rem;
    }
  }
`;
export default ExpertRegistration;
